<template>
  <v-container>
    <h3>Kolekcja zawiera</h3>
    <v-divider></v-divider>
    <h1>{{ booksCount }}</h1>
    <h3>{{ declineBook(booksCount) }}</h3>
    <v-divider></v-divider>
    <h1>{{ authorsCount }}</h1>
    <h3>{{ declineAuthor(authorsCount) }}</h3>
    <v-divider></v-divider>
    <h1>{{ pagesCount }}</h1>
    <h3>{{ declinePage(pagesCount) }}</h3>
    <v-divider></v-divider>
    <h1>12.6</h1>
    <h3>metrw biecych</h3>
    <v-divider></v-divider>
    <h1>{{ quotesCount }}</h1>
    <h3>{{ declineQuote(quotesCount) }}</h3>
    <v-divider></v-divider>
  </v-container>
</template>

<script>
import API from "../api.js";
import { declineBook, declineQuote, declineAuthor, declinePage } from "../utils/declination";

export default {
  data() {
    return {
      quotesTagSearch: [],
      booksCount: 0,
      quotesCount: 0,
      authorsCount: 0,
      pagesCount: 452,
    };
  },
  methods: {
    declineBook(bookCount) {
      return declineBook(bookCount);
    },
    declineQuote(quoteCount) {
      return declineQuote(quoteCount);
    },
    declineAuthor(authorCount) {
      return declineAuthor(authorCount);
    },
    declinePage(pageCount) {
      return declinePage(pageCount);
    },
  },
  async created() {
    const bookResponse = await API.getBooksCount();
    this.booksCount = bookResponse;

    const quoteResponse = await API.getAllQuotes();
    this.quotesCount = quoteResponse.length;

    const authorResponse = await API.getValuesFromFields("authors");
    this.authorsCount = authorResponse.size;

    const pagesResponse = await API.getPagesSum();
    this.pagesCount = pagesResponse;
  },
};
</script>

<style scoped>
h3 {
  text-align: center;
}
h1 {
  text-align: center;
  margin-top: 7px;
}
</style>
