<template>
  <v-container>
    <v-card class="mx-auto mt-3" width="800" :elevation="hover ? 24 : 6">
      <v-row class="ml-0">
        <v-card-title class="headline">{{ book.title }}</v-card-title>
      </v-row>
      <v-row class="ml-2">
        <router-link class="author" :to="{ name: 'books-author', params: { author: author } }" v-for="(author, i) in authors" :key="i">
          {{ author }}{{ commaInbetween(i, authors) }}
        </router-link>
      </v-row>

      <v-row>
        <v-col cols="8">
          <v-card-text>
            <v-row>
              <v-col cols="4">
                <div>Wydawnictwo:</div>
                <div>Rok wydania:</div>
                <div>liczba stron:</div>
                <div>Oprawa:</div>
                <div>Cena zakupu:</div>
                <div>Cena detaliczna:</div>
                <div>Data zakupu:</div>
                <div>Przeczytana:</div>
                <div>Tumacz:</div>
                <div>Tytu oryginalny:</div>
                <div></div>
              </v-col>
              <v-col cols="8">
                <div>{{ book.publisher }}</div>
                <div>{{ book.publicationDate }}</div>
                <div>{{ book.pages }}</div>
                <div>{{ book.cover }}</div>
                <div>{{ book.purchasePrice.toFixed(2) }} z</div>
                <div>{{ book.retailPrice.toFixed(2) }} z</div>
                <div>{{ book.purchaseDate }}</div>
                <div>{{ book.read ? "tak" : "nie" }}</div>
                <span v-for="(translator, i) in translators" :key="i">{{ attributesWithComma(i, translators) }} </span>
                <div>{{ book.originalTitle }}</div>
                <div></div>
              </v-col>
            </v-row>
          </v-card-text>
        </v-col>

        <v-col cols="4">
          <!-- <v-img class="mt-3 mb-3 mx-n3" max-height="300" max-width="220" :src="`/${book.image}`"></v-img> -->
          <v-card class="mt-n5 ml-5" max-width="200" elevation="10">
            <v-img max-height="300" max-width="200" :src="`/${book.image}`"></v-img>
          </v-card>
        </v-col>
      </v-row>
      <v-card-subtitle class="mt-n2">
        <v-icon>mdi-tag</v-icon>
        <v-chip class="ml-1 mb-1" v-for="(tag, i) in tags" :key="i" :to="{ name: 'books-tag', params: { tag: tag } }">
          {{ tag }}
        </v-chip>
      </v-card-subtitle>
      <v-row v-if="logged">
        <v-col class="d-flex justify-end mx-n3">
          <v-btn color="blue" text :to="{ name: 'add-quote', params: { id: book._id } }">Dodaj cytat</v-btn>
          <v-btn color="success" text :to="{ name: 'edit-book', params: { id: book._id } }">Edytuj</v-btn>
          <v-btn color="red" text @click="removeBook(book._id)">Usu</v-btn>
        </v-col>
      </v-row>
    </v-card>
    <v-divider class="ml-5 mt-5"></v-divider>
    <h2 v-if="quotes.length != 0">Cytaty</h2>
    <quote-tile v-for="quote in quotes" :key="quote.id" :quote="quote"></quote-tile>
  </v-container>
</template>

<script>
import API from "../api";
import { attributesWithComma, commaInbetween } from "../utils/declination";

export default {
  data() {
    return {
      book: {
        purchasePrice: 0,
        retailPrice: 0,
      },
      quotes: [],
      authors: [],
      translators: [],
      tags: [],
      hover: "",
      logged: false,
    };
  },
  async created() {
    if (localStorage.getItem("token") !== null) {
      this.logged = true;
    }

    const bookResponse = await API.getBookById(this.$route.params.id);
    this.book = bookResponse;
    this.book.purchaseDate = bookResponse.purchaseDate.substr(0, 10);
    const quoteResponse = await API.getQuotesByBookId(this.book._id);
    this.quotes = quoteResponse;
    this.authors = this.book.authors.split(",");
    this.translators = this.book.translators.split(",");
    this.tags = this.book.tags.split(",");
  },
  methods: {
    async removeBook(id) {
      const response = await API.deleteBook(id);
      this.$router.push({ name: "books", params: { message: response.message } });
    },
    attributesWithComma(index, array) {
      return attributesWithComma(index, array);
    },
    commaInbetween(index, array) {
      return commaInbetween(index, array);
    },
  },
};
</script>

<style scoped>
h2 {
  text-align: center;
  margin-top: 1pc;
}
.tags {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 5px;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.tags-first {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 2pc;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.author {
  all: unset;
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  margin-left: 8px;
  margin-top: -1pc;
  margin-bottom: -1pc;
  cursor: pointer;
}
</style>
