<template>
  <v-card class="mx-auto mt-6" max-width="900" min-height="200" rounded="lg" :elevation="hover ? 24 : 6">
    <v-row>
      <v-col cols="10">
        <router-link class="title2" :to="bookUrl">
          {{ book.title }}
        </router-link>
        <div class="ml-2">
          <router-link class="author2" :to="{ name: 'books-author', params: { author: author } }" v-for="(author, i) in authors" :key="i">
            {{ author }}{{ commaInbetween(i, authors) }}
          </router-link>
        </div>
        <v-card-text>
          <v-container class="ml-n4 mt-n4 mb-n4">
            <v-row dense>
              <v-col cols="2">
                <div>Rok wydania:</div>
                <div>liczba stron:</div>
                <div>Oprawa:</div>
              </v-col>
              <v-col cols="2" class="col2">
                <div>{{ book.publicationDate }}</div>
                <div>{{ book.pages }}</div>
                <div>{{ book.cover }}</div>
              </v-col>
              <v-col cols="3" class="col3">
                <div>Wydawnictwo:</div>
                <div>Tumacz:</div>
                <div>Tytu oryginalny:</div>
              </v-col>
              <v-col cols="6" class="col4">
                <div>{{ book.publisher }}</div>
                <span v-for="(translator, i) in translators" :key="i">{{ attributesWithComma(i, translators) }} </span>
                <div>{{ book.originalTitle }}</div>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-col>

      <v-col cols="2">
        <v-card class="ml-n5 mt-2" elevation="10" :to="{ name: 'book', params: { id: book._id } }">
          <v-img max-height="190" max-width="150" :src="`/${book.image}`"></v-img>
        </v-card>
      </v-col>
    </v-row>
    <v-row class="ml-2" dense>
      <v-col cols="1" class="mt-3 ml-1"><v-icon>mdi-tag</v-icon></v-col>
      <v-col cols="11" class="ml-n10">
        <v-chip-group>
          <v-chip class="mb-1" v-for="(tag, i) in tags" :key="i" :to="{ name: 'books-tag', params: { tag: tag } }">
            {{ tag }}
          </v-chip>
        </v-chip-group>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import { attributesWithComma, commaInbetween } from "../utils/declination";

export default {
  name: "BookTile",
  props: ["book"],
  data() {
    return {
      hover: "",
      bookUrl: `/book/${this.book._id}`,
      authors: [],
      translators: [],
      tags: [],
    };
  },
  methods: {
    attributesWithComma(index, array) {
      return attributesWithComma(index, array);
    },
    commaInbetween(index, array) {
      return commaInbetween(index, array);
    },
  },
  created() {
    this.authors = this.book.authors.split(",");
    this.translators = this.book.translators.split(",");
    this.tags = this.book.tags.split(",");
  },
};
</script>

<style scoped>
.title2 {
  all: unset;
  cursor: pointer;
  font: bold 25px Roboto;
  margin-left: 10px;
}
.author {
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  margin-left: 2px;
  margin-top: 0px;
}
.author2 {
  all: unset;
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  margin-left: 2px;
  margin-top: 0px;
  cursor: pointer;
}
.tags {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 5px;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.tags-first {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 10px;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.col2 {
  font-weight: bold;
  margin-left: -1pc;
}
.col3 {
  font-weight: normal;
  margin-left: -2pc;
}
.col4 {
  font-weight: bold;
  margin-left: -3pc;
}
</style>
