import axios from "axios";

const url = "/api/book";
const quoteUrl = "/api/quote";
const userUrl = "/api/user"

export default class API {
  static async getAllBooks() {
    const res = await axios.get(url);
    return res.data;
  }

  static async getBookById(id) {
    const res = await axios.get(`${url}/${id}`);
    return res.data;
  }

  static async addBook(book) {
    const res = await axios.post(url, book);
    return res.data;
  }

  static async updateBook(id, book) {
    const res = await axios.patch(`${url}/${id}`, book);
    return res.data;
  }

  static async deleteBook(id) {
    const res = await axios.delete(`${url}/${id}`);
    return res.data;
  }

  static async getAutocompleteData() {
    let data = {
      authors: new Set(),
      publishers: new Set(),
      translators: new Set(),
      tags: new Set(),
    };
    data.authors = await this.getValuesFromFields("authors");
    data.publishers = await this.getValuesFromFields("publishers");
    data.translators = await this.getValuesFromFields("translators");
    data.tags = await this.getValuesFromFields("tags");
    return data;
  }

  // returns set of distinct values from given field
  static async getValuesFromFields(fieldName) {
    const res = await axios.get(`${url}/param/${fieldName}`);
    let values = [];
    switch (fieldName) {
      case "authors":
        res.data.forEach((data) => values.push(data.authors.split(",")));
        break;
      case "translators":
        res.data.forEach((data) => values.push(data.translators.split(",")));
        break;
      case "tags":
        res.data.forEach((data) => values.push(data.tags.split(",")));
        break;
      case "publishers":
        res.data.forEach((data) => values.push(data.publisher.split(",")));
        break;
    }
    const flattenValues = values.flat(1);
    return new Set(flattenValues);
  }

  static async getBooksCount() {
    const res = await axios.get(url + "/count");
    return res.data;
  }

  static async getPagesSum() {
    const res = await axios.get(url);
    let sum = 0;
    res.data.forEach((data) => (sum += data.pages));
    return sum;
  }

  static async getFieldValuesCountSortedDesc(fieldName) {
    const res = await axios.get(`${url}/param/${fieldName}`);
    let values = [];
    switch (fieldName) {
      case "authors":
        res.data.forEach((data) => values.push(data.authors.split(",")));
        break;
      case "translators":
        res.data.forEach((data) => values.push(data.translators.split(",")));
        break;
      case "tags":
        res.data.forEach((data) => values.push(data.tags.split(",")));
        break;
      case "publishers":
        res.data.forEach((data) => values.push(data.publisher.split(",")));
        break;
    }
    const flattenValues = values.flat(1);
    const counts = {};

    flattenValues.forEach((el) => {
      counts[el] = counts[el] ? (counts[el] += 1) : 1;
    });
    const countsSorted = Object.entries(counts).sort(([_, a], [__, b]) => b - a);

    return countsSorted;
  }

  static async getBooksByYear(year) {
    const res = await axios.get(url);
    return res.data.filter((data) => new Date(data.purchaseDate).getFullYear() == year);
  }

  static async sumExpensesForYear(year) {
    const books = await this.getBooksByYear(year);
    let sum = 0;
    books.forEach((book) => (sum += book.purchasePrice));
    return sum;
  }

  static async getCountedBooksByReadStatus(status) {
    const res = await axios.get(url);
    return res.data.filter((data) => data.read === status).length;
  }

  static async getNewestBooks() {
    const res = await axios.get(url + "/crDate");
    return res.data;
  }

  //------------------------------------------------------------

  static async getAllQuotes() {
    const res = await axios.get(quoteUrl);
    return res.data;
  }

  static async getQuoteById(id) {
    const res = await axios.get(`${quoteUrl}/${id}`);
    return res.data;
  }

  static async getQuotesByBookId(bookId) {
    const res = await axios.get(quoteUrl);
    return res.data.filter((quote) => quote.bookId === bookId);
  }

  static async addQuote(quote) {
    const res = await axios.post(quoteUrl, quote);
    return res.data;
  }

  static async updateQuote(id, quote) {
    const res = await axios.patch(`${quoteUrl}/${id}`, quote);
    return res.data;
  }

  static async deleteQuote(id) {
    const res = await axios.delete(`${quoteUrl}/${id}`);
    return res.data;
  }

  static async getAutocompleteQuoteData() {
    let data = {
      authors: new Set(),
      tags: new Set(),
    };
    const res = await axios.get(quoteUrl);
    const quotes = res.data;

    quotes.forEach((quote) => {
      let authors = quote.authors?.split(",");
      let tags = quote.tags?.split(",");
      authors.forEach((author) => data.authors.add(author));
      tags.forEach((tag) => data.tags.add(tag));
    });

    return data;
  }

  //----------------USER------------------

  static async login(user) {
    const res = await axios.post(`${userUrl}/login/`, user);
    if(res.status === 200) {
      localStorage.setItem("token", res.data.token);
      return res.data;
    }
    return null;
  }
}
