<template>
  <v-container>
    <v-row no-gutters>
      <v-col sm="10" class="mx-auto">
        <v-card class="pa-5">
          <v-card-title>Dodaj cytat</v-card-title>
          <v-divider></v-divider>
          <v-form ref="form" @submit.prevent="submitForm" class="pa-5" enctype="multipart/form-data">
            <v-text-field label="Tytu" v-model="quote.bookTitle" :rules="rules"></v-text-field>
            <v-combobox v-model="quote.authors" label="Autor" multiple chips :items="authorSearch" :rules="rules"></v-combobox>
            <v-combobox v-model="quote.tags" label="Tagi" multiple chips :items="quotesTagSearch"></v-combobox>
            <v-textarea v-model="quote.text" clearable clear-icon="mdi-close-circle" label="Tekst" value=""></v-textarea>

            <v-btn type="submit" class="mt-3" color="primary">Dodaj cytat</v-btn>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import API from "../api";

export default {
  data() {
    return {
      rules: [(value) => !!value || "To pole jest wymagane."],
      book: "",
      quote: {
        bookTitle: "",
        authors: "",
        bookId: "",
        text: "",
        tags: "",
      },
      authorSearch: [],
      quotesTagSearch: [],
    };
  },
  methods: {
    async submitForm() {
      const formData = new FormData();
      formData.append("bookTitle", this.quote.bookTitle);
      formData.append("authors", this.quote.authors);
      formData.append("bookId", this.quote.bookId);
      formData.append("text", this.quote.text);
      formData.append("tags", this.quote.tags);
      if (this.$refs.form.validate()) {
        const response = await API.addQuote(formData);
        this.$router.push({
          name: "quotes",
          params: { message: response.message },
        });
      }
    },
  },
  async created() {
    if (localStorage.getItem("token") === null) {
      this.$router.push('/login');
    }
    if (this.$route.params.id) {
      const response = await API.getBookById(this.$route.params.id);
      this.book = response;
      this.quote.bookId = this.book._id;
      this.quote.bookTitle = this.book.title;
      this.quote.authors = this.book.authors.split(",");
    }
    const data = await API.getAutocompleteQuoteData();
    this.authorSearch = Array.from(data.authors);
    this.quotesTagSearch = Array.from(data.tags);
  },
};
</script>
