export function declineBook(booksCount) {
  if (booksCount === 1) {
    return "ksika";
  } else if (booksCount % 10 === 2 || booksCount % 10 === 3 || booksCount % 10 === 4) {
    return "ksiki";
  }
  return "ksiek";
}

export function declineQuote(quotesCount) {
  if (quotesCount === 1) {
    return "cytat";
  } else if (quotesCount % 10 === 2 || quotesCount % 10 === 3 || quotesCount % 10 === 4) {
    return "cytaty";
  }
  return "cytatw";
}

export function declinePage(pagesCount) {
  if (pagesCount === 1) {
    return "stron";
  } else if (pagesCount % 10 === 2 || pagesCount % 10 === 3 || pagesCount % 10 === 4) {
    return "strony";
  }
  return "stron";
}

export function declineAuthor(authorsCount) {
  if (authorsCount === 1) {
    return "autora";
  }
  return "autorw";
}

export function attributesWithComma(index, array) {
  if (index !== array.length - 1) {
    return `${array[index]},`;
  } else {
    return array[index];
  }
}

export function commaInbetween(index, array) {
  if (index !== array.length - 1) {
    return ", ";
  }
}
