<template>
  <v-app id="inspire">
    <v-app-bar app color="white" flat>
      <v-tabs centered class="ml-n9" color="grey darken-1">
        <v-tab v-for="link in filterLinks()" :key="link.title" :to="link.link">
          {{ link.title }}
        </v-tab>
      </v-tabs>
      <v-btn v-if="loggedIn" @click="logout">Logout</v-btn>
      <router-link v-if="!loggedIn" :to="`/login`">
        <v-icon large>mdi-cog</v-icon>
      </router-link>
    </v-app-bar>

    <v-main class="grey lighten-3">
      <v-container>
        <v-row>
          <v-col cols="2">
            <v-sheet rounded="lg" min-height="300">
              <info-banner></info-banner>
            </v-sheet>
          </v-col>

          <v-col cols="10">
            <v-sheet min-height="70vh" max-width="1000" rounded="lg">
              <router-view></router-view>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import InfoBanner from "./components/InfoBanner";

export default {
  components: { InfoBanner },
  data: () => ({
    links: [
      { title: "Home", link: "/", hidden: false },
      { title: "Ksigozbir", link: "/books", hidden: false },
      { title: "Cytaty", link: "/quotes", hidden: false },
      { title: "Dodaj ksik", link: "/add-book", hidden: true },
      { title: "Dodaj cytat", link: "/add-quote", hidden: true },
      { title: "Statystyki", link: "/statistics", hidden: false },
      { title: "Info", link: "/about", hidden: false },
    ],
    loggedIn: false,
  }),
  watch: {
    $route: function() {
      if (this.$route.name === "not-pass") {
        this.reload();
      }
    },
  },
  methods: {
    logout() {
      localStorage.clear();
      this.loggedIn = false;
      this.reload();
      this.$router.push("/");
    },
    reload() {
      if (localStorage.getItem("token") !== null) {
        this.loggedIn = true;
        this.links[3].hidden = false;
        this.links[4].hidden = false;
      } else {
        this.links[3].hidden = true;
        this.links[4].hidden = true;
      }

      this.$forceUpdate();
    },
    filterLinks() {
      return this.links.filter((link) => link.hidden === false);
    },
  },
  async created() {
    if (localStorage.getItem("token") !== null) {
      this.links[3].hidden = false;
      this.links[4].hidden = false;
      this.loggedIn = true;
    } else {
    }
  },
};
</script>
