<template>
  <v-card class="mx-auto mt-4" rounded="lg" width="800" :elevation="hover ? 24 : 6">
    <v-card-subtitle
      >{{ quote.bookTitle }} - <span v-for="(author, i) in authors" :key="i">{{ attributesWithComma(i, authors) }}</span>
    </v-card-subtitle>
    <v-card-text class="headline font-weight-bold">
      {{ quote.text }}
    </v-card-text>
    <v-card-subtitle class="mt-1 mb-n1">
        <v-icon>mdi-tag</v-icon>
        <v-chip class="ml-1 mb-1" v-for="(tag, i) in tags" :key="i" :to="{ name: 'quotes-tag', params: { tag: tag } }">
          {{ tag }}
        </v-chip>
      </v-card-subtitle>
    <v-row v-if="logged">
      <v-col class="d-flex justify-end mx-n3">
        <v-btn color="success" text :to="{ name: 'edit-quote', params: { id: quote._id } }">Edytuj</v-btn>
        <v-btn color="red" text @click="removeQuote(quote._id)">Usu</v-btn>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import API from "../api";
import { attributesWithComma } from "../utils/declination";

export default {
  name: "QuoteTile",
  props: ["quote"],
  data() {
    return {
      hover: "",
      authors: [],
      tags: [],
      logged: false,
    };
  },
  methods: {
    attributesWithComma(index, array) {
      return attributesWithComma(index, array);
    },
    async removeQuote(id) {
      const response = await API.deleteQuote(id);
      this.$router.push({ name: "books", params: { message: response.message } });
    },
  },
  created() {
    if (localStorage.getItem("token") !== null) {
      this.logged = true;
    }

    this.authors = this.quote.authors.split(",");
    this.tags = this.quote.tags.split(",");
  },
};
</script>

<style scoped>
.fill-width {
  overflow-x: auto;
  flex-wrap: nowrap;
  margin: 0;
  padding: 0;
}
.title {
  font-size: 30px;
  margin-left: 10px;
  font-family: Century Gothic, sans-serif;
  margin-top: 0px;
}
.author {
  color: rgb(78, 77, 77);
  font: 18px Georgia, serif;
  margin-left: 10px;
  margin-top: 0px;
}
.tags {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 5px;
  margin-top: 0px;
  margin-bottom: -2pc;
}
.tags-first {
  color: rgb(78, 77, 77);
  font: 15px Georgia, serif;
  margin-left: 1pc;
  margin-top: 0px;
  margin-bottom: -2pc;
}
</style>
