<template>
  <v-form>
    <v-container>
      <v-row class="mt-10" dense>
        <v-col></v-col>
        <v-col cols="4">
          <v-row dense>
            <v-text-field v-model="user.name" label="login" outlined></v-text-field>
          </v-row>
          <v-row dense>
            <v-text-field
              v-model="user.password"
              :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show3 ? 'text' : 'password'"
              name="input-10-2"
              label="password"
              value=""
              class="input-group--focused"
              outlined
              @click:append="show3 = !show3"
            ></v-text-field>
          </v-row>
          <v-row dense>
            <v-btn block @click="login()">login</v-btn>
          </v-row>
        </v-col>
        <v-col></v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import API from "../api";

export default {
  name: "Login",
  data() {
    return {
      user: {
        name: "",
        password: "",
      },
      show3: false,
    };
  },
  methods: {
    async login() {
      let user = this.user;
      const response = await API.login(user);
      this.$router.push("/not-pass");
    },
  },
  created() {},
};
</script>
