<template>
  <v-container>
    <h1>Ksigozbir</h1>
    <v-divider></v-divider>

    <v-container>
      <v-row dense>
        <v-col>
          <v-text-field v-model="filter.author" label="Autor"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-model="filter.title" label="Tytu"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-model="filter.publisher" label="Wydawnictwo"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-model="filter.translator" label="Tumacz"></v-text-field>
        </v-col>
      </v-row>

      <v-row dense no-gutters>
        <v-col sm="4" class="mt-n4">
          <v-checkbox v-model="filter.isDateFilterEneabled" label="Filtruj po dacie zakupu"></v-checkbox>
        </v-col>
        <v-col col="4">
          <v-menu v-model="menu1" :close-on-content-click="false" transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="filter.purchaseDateFrom"
                label="Data zakupu od"
                prepend-icon="mdi-calendar"
                :disabled="!filter.isDateFilterEneabled"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="filter.purchaseDateFrom" @input="menu1 = false"></v-date-picker>
          </v-menu>
        </v-col>
        <v-col col="4">
          <v-menu v-model="menu2" :close-on-content-click="false" transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="filter.purchaseDateTo"
                label="Data zakupu do"
                prepend-icon="mdi-calendar"
                :disabled="!filter.isDateFilterEneabled"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="filter.purchaseDateTo" @input="menu2 = false"></v-date-picker>
          </v-menu>
        </v-col>
      </v-row>

      <v-row dense no-gutters>
        <v-col cols="6" class="mt-n2">
          <v-autocomplete dense v-model="filter.tags" multiple chips :items="tagsSearch" label="Tagi"></v-autocomplete>
        </v-col>
        <v-col sm="2"></v-col>
        <v-col sm="2" class="mt-n4">
          <v-checkbox v-model="filter.read" label="Przeczytane"></v-checkbox>
        </v-col>
        <v-col sm="2" class="mt-n4">
          <v-checkbox v-model="filter.notRead" label="Nieprzeczytane"></v-checkbox>
        </v-col>
      </v-row>

      <v-row dense>
        <v-col cols="9"></v-col>
        <v-col cols="3" class="mt-n2">
          <v-select :items="items" label="Sortuj wg" v-model="sort"></v-select>
        </v-col>
      </v-row>
    </v-container>

    <v-divider></v-divider>
    <v-alert border="left" close-text="Close Alert" color="green accent-4" dark dismissible v-if="this.$route.params.message">
      {{ this.$route.params.message }}
    </v-alert>
    <div>Wynik wyszukiwania: {{ filterAndSortBooks.length }} {{ declineBook(filterAndSortBooks.length) }}</div>
    <v-divider></v-divider>
    <v-pagination
      v-model="pagination.page"
      :length="Math.ceil(filterAndSortBooks.length / pagination.perPage)"
      :circle="pagination.circle"
      :total-visible="pagination.totalVisible"
      @input="updatePage(pagination.page)"
    ></v-pagination>

    <book-tile v-for="book in visibleBooks" :key="book._id" :book="book"></book-tile>
    <div class="niema" v-if="filterAndSortBooks.length == 0">Nie znaleziono ksiek o podaych kryteriach.</div>
    <v-pagination
      class="mt-5"
      v-model="pagination.page"
      :length="Math.ceil(filterAndSortBooks.length / pagination.perPage)"
      :circle="pagination.circle"
      :total-visible="pagination.totalVisible"
      @input="
        updatePage(pagination.page);
        scrollToTop();
      "
    ></v-pagination>
  </v-container>
</template>

<script>
import API from "../api";
import BookTile from "../components/BookTile.vue";
import { declineBook } from "../utils/declination";

export default {
  components: { BookTile },
  name: "Books",
  data() {
    return {
      books: [],
      visibleBooks: [],
      items: ["Sortuj wg", "Data zakupu - od najnowszej", "Data zakupu - od najstarszej"],
      sort: "Data zakupu - od najnowszej",
      filter: {
        purchaseDateFrom: new Date("2000-01-01").toISOString().substr(0, 10),
        purchaseDateTo: new Date().toISOString().substr(0, 10),
        title: "",
        author: "",
        publisher: "",
        translator: "",
        read: true,
        notRead: true,
        tags: [],
        isDateFilterEneabled: false,
      },
      pagination: {
        page: 1,
        perPage: 10,
        circle: true,
        totalVisible: 9,
      },
      tagsSearch: [],
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      menu1: false,
      menu2: false,
    };
  },
  computed: {
    filterAndSortBooks: function() {
      let books = [];
      books = this.books
        .filter((book) => book.authors.toLowerCase().includes(this.filter.author.toLowerCase()))
        .filter((book) => book.title.toLowerCase().includes(this.filter.title.toLowerCase()))
        .filter((book) => book.publisher.toLowerCase().includes(this.filter.publisher.toLowerCase()))
        .filter((book) => book.translators.toLowerCase().includes(this.filter.translator.toLowerCase()))
        .filter((book) => book.read === this.filter.read || book.read != this.filter.notRead)
        .filter((book) => this.searchTags(book, this.filter.tags));

      if (this.filter.isDateFilterEneabled) {
        books = books.filter((book) => book.purchaseDate > this.filter.purchaseDateFrom && book.purchaseDate < this.filter.purchaseDateTo);
      }

      switch (this.sort) {
        case this.items[1]:
          books.sort((a, b) => (a.purchaseDate < b.purchaseDate ? 1 : -1));
          break;
        case this.items[2]:
          books.sort((a, b) => (a.purchaseDate > b.purchaseDate ? 1 : -1));
          break;
        default:
          return books;
      }
      return books;
    },
  },
  watch: {
    filterAndSortBooks: function() {
      this.updatePage(1);
    },
    $route: function() {
      if (this.$route.params.tag) {
        this.filter.tags = [];
        this.filter.author = "";
        this.filter.tags.push(this.$route.params.tag);
      }
      if (this.$route.params.author) {
        this.filter.tags = [];
        this.filter.author = "";
        this.filter.author = this.$route.params.author;
      }
    },
  },
  methods: {
    declineBook(booksCount) {
      return declineBook(booksCount);
    },

    searchTags(book, tags) {
      let flag = true;
      tags.forEach((tag) => {
        if (!book.tags.toLowerCase().includes(tag.toLowerCase())) {
          flag = false;
        }
      });
      return flag;
    },

    updatePage(pageNumber) {
      this.pagination.page = pageNumber;
      this.updateVisibleBooks();
    },

    updateVisibleBooks() {
      this.visibleBooks = this.filterAndSortBooks.slice(
        (this.pagination.page - 1) * this.pagination.perPage,
        (this.pagination.page - 1) * this.pagination.perPage + this.pagination.perPage
      );

      if (this.visibleBooks.length == 0 && this.pagination.page > 0) {
        this.updatePage(this.pagination.page - 1);
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
  async created() {
    this.books = await API.getAllBooks();
    const data = await API.getAutocompleteData();
    this.tagsSearch = Array.from(data.tags);
    if (this.$route.params.tag) {
      this.filter.tags.push(this.$route.params.tag);
    }
    if (this.$route.params.author) {
      this.filter.author = this.$route.params.author;
    }
    this.updatePage(1);
  },
};
</script>

<style>
.v-text-field {
  margin: 0;
  padding: 0;
}
h1 {
  text-align: center;
}
.niema {
  text-align: center;
}
</style>
