<template>
  <v-container>
    <h1>Cytaty</h1>
    <v-divider></v-divider>

    <v-container>
      <v-row dense>
        <v-col>
          <v-text-field v-model="filter.author" label="Autor"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-model="filter.title" label="Tytu"></v-text-field>
        </v-col>
      </v-row>
      <v-row dense>
        <v-col cols="6">
          <v-autocomplete class="mt-n2" dense v-model="filter.tags" multiple chips :items="quotesTagSearch" label="Tagi"></v-autocomplete>
        </v-col>
        <v-col cols="3"></v-col>
        <v-col cols="3">
          <v-select :items="items" label="Sortuj wg" v-model="sort"></v-select>
        </v-col>
      </v-row>
    </v-container>

    <v-divider></v-divider>
    <v-alert border="left" close-text="Close Alert" color="green accent-4" dark dismissible v-if="this.$route.params.message">
      {{ this.$route.params.message }}
    </v-alert>
    <v-divider></v-divider>
    <v-pagination
      v-model="pagination.page"
      :length="Math.ceil(filterAndSortQuotes.length / pagination.perPage)"
      :circle="pagination.circle"
      :total-visible="pagination.totalVisible"
      @input="updatePage(pagination.page)"
    ></v-pagination>
    <quote-tile v-for="quote in visibleQuotes" :key="quote._id" :quote="quote"></quote-tile>
    <v-pagination
      class="mt-5"
      v-model="pagination.page"
      :length="Math.ceil(filterAndSortQuotes.length / pagination.perPage)"
      :circle="pagination.circle"
      :total-visible="pagination.totalVisible"
      @input="
        updatePage(pagination.page);
        scrollToTop();
      "
    ></v-pagination>
  </v-container>
</template>

<script>
import API from "../api";
import QuoteTile from "../components/QuoteTile.vue";

export default {
  components: { QuoteTile },
  name: "Quotes",
  data() {
    return {
      quotes: [],
      visibleQuotes: [],
      items: ["Sortuj wg", "Data dodania - od najnowszej", "Data dodania - od najstarszej"],
      sort: "Sortuj wg",
      filter: {
        bookTtitle: "",
        author: "",
        created: "",
        tags: [],
      },
      pagination: {
        page: 1,
        perPage: 10,
        circle: true,
        totalVisible: 9,
      },
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      menu1: false,
      menu2: false,
      quotesTagSearch: [],
    };
  },
  computed: {
    filterAndSortQuotes: function() {
      let quotes = [];
      quotes = this.quotes
        .filter((quote) => quote.authors.toLowerCase().includes(this.filter.author.toLowerCase()))
        .filter((quote) => quote.bookTitle.toLowerCase().includes(this.filter.bookTtitle.toLowerCase()))
        .filter((quote) => this.searchTags(quote, this.filter.tags));

      switch (this.sort) {
        case this.items[1]:
          quotes.sort((a, b) => (a.created < b.created ? 1 : -1));
          break;
        case this.items[2]:
          quotes.sort((a, b) => (a.created > b.created ? 1 : -1));
          break;
        default:
          return quotes;
      }

      return quotes;
    },
  },
  watch: {
    filterAndSortQuotes: function() {
      this.updatePage(1);
    },
    $route: function() {
      if (this.$route.params.tag) {
        this.filter.tags = [];
        this.filter.tags.push(this.$route.params.tag);
      }
    },
  },
  methods: {
    searchTags(quote, tags) {
      let flag = true;
      tags.forEach((tag) => {
        if (!quote.tags.toLowerCase().includes(tag.toLowerCase())) {
          flag = false;
        }
      });
      return flag;
    },
    updatePage(pageNumber) {
      this.pagination.page = pageNumber;
      this.updateVisibleQuotes();
    },

    updateVisibleQuotes() {
      this.visibleQuotes = this.filterAndSortQuotes.slice(
        (this.pagination.page - 1) * this.pagination.perPage,
        (this.pagination.page - 1) * this.pagination.perPage + this.pagination.perPage
      );

      if (this.visibleQuotes.length == 0 && this.pagination.page > 0) {
        this.updatePage(this.pagination.page - 1);
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
  async created() {
    this.quotes = await API.getAllQuotes();
    const data = await API.getAutocompleteQuoteData();
    this.quotesTagSearch = Array.from(data.tags);
    if (this.$route.params.tag) {
      this.filter.tags.push(this.$route.params.tag);
    }
    this.updatePage(1);
  },
};
</script>

<style>
.v-text-field {
  margin: 0;
  padding: 0;
}
</style>
