import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import AddBook from '../views/AddBook.vue'
import Books from '../views/Books.vue'
import BookDetails from '../views/BookDetails.vue'
import EditBook from '../views/EditBook.vue'
import EditQuote from '../views/EditQuote.vue'
import AddQuote from '../views/AddQuote'
import Quotes from '../views/Quotes'
import Statistics from '../views/Statistics'
import Login from '../views/Login'
import NotPass from '../views/NotPass'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/add-book',
    name: 'add-book',
    component: AddBook
  },
  {
    path: '/books',
    name: 'books',
    component: Books
  },
  {
    path: '/book/:id',
    name: 'book',
    component: BookDetails
  },
  {
    path: '/edit-book/:id',
    name: 'edit-book',
    component: EditBook
  },
  {
    path: '/books/:tag',
    name: 'books-tag',
    component: Books
  },
  {
    path: '/books/:author',
    name: 'books-author',
    component: Books
  },
  {
    path: '/add-quote',
    name: 'add-quote',
    component: AddQuote
  },
  {
    path: '/add-quote/:id',
    name: 'add-quote',
    component: AddQuote
  },
  {
    path: '/quotes',
    name: 'quotes',
    component: Quotes
  },
  {
    path: '/edit-quote/:id',
    name: 'edit-quote',
    component: EditQuote
  },
  {
    path: '/quotes/:tag',
    name: 'quotes-tag',
    component: Quotes
  },
  {
    path: '/statistics',
    name: 'statistics',
    component: Statistics
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/not-pass',
    name: 'not-pass',
    component: NotPass
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
