<template>
  <v-container>
    <h1>Statystyki</h1>

    <h2>Przeczytane ksiki</h2>
    <v-row>
      <v-col cols="2"></v-col>
      <v-col>
        <v-progress-linear v-model="progress" height="25">
          <strong>{{ readBooksNumber }} / {{ allBooksCounted }} ({{ Math.ceil(progress) }}%)</strong>
        </v-progress-linear>
      </v-col>
      <v-col cols="2"></v-col>
    </v-row>

    <h2>Liczba ksiek wg autorw</h2>
    <bar-chart :data="authorsCounted"></bar-chart>

    <h2>Liczba ksiek wg wydawnictw</h2>
    <bar-chart :data="publishersCounted"></bar-chart>

    <h2>Liczba ksiek zakupiona w danym roku</h2>
    <column-chart :data="booksByYear"></column-chart>

    <h2>Roczne wydatki</h2>
    <column-chart :data="expensesByYear" suffix=" z"></column-chart>
  </v-container>
</template>

<script>
import API from "../api";

export default {
  name: "Statistics",
  data() {
    return {
      authorsCounted: [],
      publishersCounted: [],
      expensesByYear: [],
      booksByYear: [],
      progress: 0,
      readBooksNumber: 0,
      allBooksCounted: 0,
    };
  },
  computed: {},
  watch: {},
  methods: {
    async getExpensesForYears(startYear, endYear) {
      let result = [];

      for (let i = startYear; i <= endYear; i++) {
        const expenses = await API.sumExpensesForYear(i);
        result.push([i, Math.round(expenses * 100) / 100]);
      }
      return result;
    },
    async getBooksCountedByYear(startYear, endYear) {
      let result = [];
      for (let i = startYear; i <= endYear; i++) {
        const booksCounted = await API.getBooksByYear(i);
        result.push([i, booksCounted.length]);
      }
      return result;
    },
  },
  async created() {
    const respo = await API.getFieldValuesCountSortedDesc("authors");
    this.authorsCounted = respo;
    this.authorsCounted.length = 20;

    const wyd = await API.getFieldValuesCountSortedDesc("publishers");
    this.publishersCounted = wyd;
    this.publishersCounted.length = 20;

    this.expensesByYear = await this.getExpensesForYears(2015, new Date().getFullYear());
    this.booksByYear = await this.getBooksCountedByYear(2015, new Date().getFullYear());

    this.readBooksNumber = await API.getCountedBooksByReadStatus(true);
    this.allBooksCounted = await API.getBooksCount();
    this.progress = (this.readBooksNumber / this.allBooksCounted) * 100;
  },
};
</script>

<style scoped>
h2 {
  text-align: center;
  margin-top: 2pc;
}
</style>
